import React from 'react'
import PropTypes from 'prop-types'

import css from './CurrencyCell.module.scss'

const CurrencyCell = ({ amount, currency, exchangedAmount, exchangedQuote }) => {
  return (
    <div className={css.container}>
      <TokenCurrencyCell amount={amount} currency={currency} />
      (<ConvertedCurrencyCell amount={exchangedAmount} quote={exchangedQuote} />)
    </div>
  )
}

CurrencyCell.propTypes = {
  amount: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  currency: PropTypes.string,
}

export default CurrencyCell